/* ----------MY CSS--------- */
/* ------------------------- */
/* ------------------------- */

:root[data-theme="light"] {
    --text: #1e1d1a;
    --background: #f8faff;
    --primary: #2c76ba;
    --secondary: #dee3f2;
    --accent: #4f94d5;
}
:root[data-theme="dark"] {
    --text: #e5e4e1;
    --background: #1f2023;
    --primary: #6298cb;
    --secondary: #1e242f;
    --accent: #005380;
}

/* https://www.realtimecolors.com/?colors=e5e4e1-1f2023-6298cb-1e242f-005380&fonts=Poppins-Poppins */

body {
    font-family: sans-serif;
    background-color: var(--primary);
    color: var(--text);
    padding: 0;
    margin: 0;
}

header {
    margin: 1rem;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    padding: 0;
    background-color: var(--primary);
    color: var(--background);
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
}

header * {
    margin: 0;
    padding: 0;
}

nav {
    width: 100vw;
    display: flex;
    background: var(--accent);
}

.navbar {
    background: var(--inherit);
    color: var(--background);
    max-width: 500px;
    overflow: scroll;

    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-around;
    align-items: center;

    padding: 0;
    margin: auto;
}

.navbarElement {
    padding: 0.7rem;
    width: 100%;
    text-align: center;
}

.navbarElement:hover {
    background-color: var(--secondary);
    color: var(--text);
}

.navbarElement .dropDown {
    display: block;
    visibility: hidden;
}

.navbarElement:hover .dropDown {
    visibility: visible;
    z-index: 2;
}

.dropDown {
    /* padding: 15px; */
    position: absolute;
    background: var(--secondary);
    display: flex;
    flex-direction: column;

    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3);
}

.dropDown .dropdownLink {
    padding: 0.5rem;
}

.dropDown .dropdownLink:hover {
    background-color: var(--accent);
    color: var(--background);
}

.dropdownLink {
    color: black
}

.dropdownLink div {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
}

#page {
    background-color: var(--background);
    /* POPE ON A SKATEBOARD!! */
    /* background-image: url("pope.png"); */
    /* background: linear-gradient(0deg, rgba(0, 0, 0, 0.50), rgba(0, 0, 0, 0.50)), url("pope.png"); */
    /* background-size: 10rem; */
    /* background-repeat: repeat; */

    display: flex;
    flex-direction: column;
    padding: 1rem;
    align-items: left;
    padding-left: 15%;
    padding-right: 15%;
}

#userPreferences {
    display: flex;
    flex-direction: row;
    justify-content:  center;
}

#userPreferences div {
    margin: 1rem;
}

main {
    /* width: 100vw; */
    width: 100%;
    max-width: 750px;
    min-height: 50vh;
}

.form {
    border: solid var(--primary) 1px;
    border-radius: 0.5rem;
    background-color: var(--secondary);
    max-width: 400px;
    margin: auto;
    justify-content: center;
}

.form div {
    display: flex;
    flex-direction: column;
    margin: 0.5rem;
    padding: 0.5rem;
}

.form ul {
    margin: auto;
    list-style:none;
}



form * {
    padding-left: 0.2rem;
    padding-right: 0.2rem;
}

input,
select,
textarea,
button {
    border: solid var(--primary) 1px;
    background-color: var(--background);
    border-radius: 0;
    padding: 0.3rem;
    margin-left: 0.2rem;
    margin-right: 0.2rem;
    margin-top: 0.1rem;
    margin-bottom: 0.1rem;
}

input:focus,
select:focus,
textarea:focus {
    transition: all 0.2s ease-in;
    outline-width: 0px;
    box-shadow: 0 0 5px var(--primary);
}

textarea:focus {
    transition: box-shadow 0.2s ease-in;
    outline-width: 0px;
    box-shadow: 0 0 5px var(--primary);
}

/* button:hover {
    
} */

.footer {
    padding: 1rem;
    background-color: var(--primary);
    color: var(--background);
}

#dashboardMenu {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: inherit;
}

.dashboardStatistic {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex: 1;

    margin: 0.5rem;
    padding: 0.2rem;
    background-color: rgba(79, 148, 213, 0.1);
    border-radius: 10px;
}

#displayObjects {
    display: flex;
    flex-direction: row;
    align-content: center;
    justify-content: center;
    flex-wrap: wrap;
}
.listObjectInfo {
    display: flex;
    flex-direction: column;
}

.listObjectInfo span {
    display: flex;
    flex-direction: row;
    justify-content: start;
}

.listObject {
    display: flex;
    flex-direction: row;
    max-height: 200px;
    margin: 0;
    padding: 0.5rem;
    flex: 1 1 45%;
}

.listObjectPic {
    /* max-width: 100px; */
    max-height: 7.5rem;
    max-width: 5.319;
    width: auto;
}

.listObject * {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
}

.listObjectName {
    padding: 0;
    margin: 0;
}

.listObjectActions {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.listObjectActionsIcons {
    font-size: inherit * 2 !important;
    padding: 0;
}

.bookAvailable {
    background-color: #d5ebff;
}

.bookUnavailable {
    background-color: #ffe2e2;
}

.objectShowExtra {
    max-height: none;
}

.objectShowExtra span, .objectShowExtra h3 {
    padding-top: 0.2rem;
    padding-bottom: 0.2rem;
}

.objectHideExtra {
    max-height: 7.5rem;
}

.objectDescription{
    padding: 0.5rem;
    font-style: italic;
    max-height: 5rem;
    overflow-y: scroll;
}

.iconButton {
    cursor: pointer;
}

.objectFilters {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

#pagesInCategory {
    font-size: 1.5rem;
}

a {
    color: var(--background);
    text-decoration: none;
}

.link {
    color: var(--accent)
}

#pagesInCategory a:visited {
    color: var(--primary);
}

#pagesInCategory a:hover {
    color: var(--text);
}

hr {
    border-color: var(--primary);
    width: 100%;
}

footer {
    display: flex;
    flex-direction: column;
}

footer * {
    flex: 1 1 100%;
}

.NotificationElement {
    padding: 1rem;
    border-radius: 1rem;
}

.NotificationElement h4 {
    margin: 0;
}

@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");


.materialSymbolsOutlined {
  font-variation-settings:
  'FILL' 0,
  'wght' 400,
  'GRAD' 0,
  'opsz' 24
}

.NotificationElementUnread {
    background: rgba(79, 148, 213, 0.2);
}

.NotificationElementRead {
    background: rgba(0, 0, 0, 0.1);
    color: rgba(0, 0, 0, 0.6)
}

.NotificationTitle {
    display:flex;
    justify-content: space-between;
}

.dashboardChart {
    background-color: var(--background);
}

.importantNotice {
    color: var(--accent) !important;
}

.validationTrue {
    color:rgb(0, 184, 71) !important;
}

.validationFalse {
    color:rgb(208, 0, 0) !important;
}

.formValidation {
    margin: auto;
    list-style:none;
}

.login {
    padding-left: auto;
    padding-right: auto;
}